.App {
  text-align: center;
}
body{
  background-image: url("https://source.unsplash.com/1600x900/?nature,water");
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.heading{
  background-color: #9cd6818f;
  display: inline-block;
  border-radius: 20px;
  font-size: 30px;
}
.main_container{
  height: 90vh;
  background-color: #D9AFD9;
background-image: linear-gradient(0deg, #D9AFD9 0%, #97D9E1 100%);
border-radius: 10px;

}
.main_container{
  overflow: auto;
}
.main_heading{
 
  display: flex;
  margin-top: 10px;
    
}
.main_heading h4{
 
   background-color: #D9AFD9;
    
}
.answer_para{
   backdrop-filter: blur(4px) saturate(180%);
    -webkit-backdrop-filter: blur(4px) saturate(180%);
    background-color: rgba(255, 255, 255, 0.46);
    border-radius: 12px;
    border: 1px solid rgba(209, 213, 219, 0.3);
}
.main_heading p{
 font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
 margin: 3px;
 margin-right: 5px;
 border: 1px solid rgb(4, 6, 151);
 background-color:orange;
 border-radius: 60px;
 width: 25px;
 height: 25px;
 cursor: pointer;
}

@media only screen and (max-width: 500px) {
  .main_heading h4{
   font-size: 12px;
}
.answer_para{
   font-size: 12px;
}
.heading{
  font-size: 15px;
}
.main_heading p{
 margin-top: 0;
 font-size: 12px;
 width: 15px;
 height: 15px;
}

}